@import "mixin_rd", "variables"

*
  transition: all .3s


.titles
  font-family: 'Lemon'
.text-body
  font-family: 'Euclid'
.text-body-bold
  font-family: 'Euclid Bold'
.text-black
  color: $color-black
.text-primary
  color: $color-primary
.text-secondary
  color: $color-secondary
.text-primary-light
  color: $color-light-primary
.text-secondary-light
  color: $color-light-secondary
.text-red
  color: #D70000
.text-gray
  color: #797878

.border-primary
  border-color: $color-primary


// COLORS BG
.bg-primary
  background-color: $color-primary
.bg-secondary
  background-color: $color-secondary
.bg-black
  background-color: $color-black
.bg-primary-light
  background-color: $color-light-primary
.bg-secondary-light
  background-color: $color-light-secondary
.bg-light-gray
  background-color: #F5F6F8
.bg-light-light
  background-color: #F7F7F7


.btn-primary
  background-color: $color-secondary
  &:hover
    background-color: #f57900
.btn-secondary
  background-color: $color-primary
  &:hover
    background-color: #02313f

// NAVBAR
.nav-bar
  background-color: #fff

.nav-item
  position: relative
  &::after
    transition: all 0.2s
    content: ''
    position: absolute
    bottom: -1.3em
    width: 0
    height: 5px
    background: $color-primary
    left: 2px
    border-radius: 15px
    +for-ipad
      bottom: -.8em
  &:hover
    color: $color-primary
    font-weight: bold
    &::after
      width: 100%

.nav-active
  color: $color-primary
  font-weight: bold
  position: relative
  &::after
    content: ''
    position: absolute
    bottom: -1.3em
    width: 100%
    height: 5px
    background: $color-primary
    left: 2px
    border-radius: 15px
    +for-ipad
      bottom: -.8em

.menu-burger
  height: 5px
  background: $color-primary
  width: 2.2em
  border-radius: 15px
  position: relative
  cursor: pointer
  transition: all .3s
  &::after,&::before
    content: ''
    height: 5px
    background: $color-primary
    width: 2.2em
    border-radius: 15px
    transition: all .3s
  &::after
    left: 0
    position: absolute
    bottom: 15px
  &::before
    left: 0
    position: absolute
    bottom: -15px

.burger-active.menu-burger
  background-color: rgba(0,0,0,0)
  &::after,&::before
    opacity: 1
  &::after
    transform: rotate(45deg)
    top: 0
    background: $color-primary
  &::before
    transform: rotate(-45deg)
    top: 0
    background: $color-primary

.nav-bar-content
  height: auto
  transition: all .2s
  +for-lg
    height: 140px
    overflow: hidden
  +for-ipad
    height: 100px
    overflow: hidden

.nav-bar-content-scroll
  transition: all .2s
  height: 95px

.carousel 
  display: inline-flex
  overflow-x: hidden
  scroll-snap-type: x mandatory
  -webkit-overflow-scrolling: touch
  scrollbar-width: none
  -ms-overflow-style: none


// CONTACTO
.input-with-placeholder input + label
  margin-top: .5em
.input-with-placeholder input:focus + label,
.input-with-placeholder input:valid + label,
.input-with-placeholder textarea:focus + label,
.input-with-placeholder textarea:valid + label
  font-size: .7em
  margin-top: 1px


// Search
.linea-search
  &::after
    content: ""
    position: absolute
    border: solid 1px #cbcbcb
    height: 2em
    top: -.4em
    right: 3em
    
// PagesNumber
.pages
  border-color: #07495D

.number
  // border-color: #07495D

.bg-gradient-emprendedor
  background-image: linear-gradient(180deg, transparent 0,rgba(0,0,0,0.3) 20%,#fff)

.content-accordion
  transition: all .5s cubic-bezier(0,1,0,1)
  max-height: 0
  overflow: hidden

.content-accordion.show
  height: auto
  max-height: 9999px
  transition: all .5s cubic-bezier(1,0,1,0)
