@font-face {
  font-family: "Lemon";
  src: url("../fonts/LEMONMILK-BoldItalic.otf");
}
@font-face {
  font-family: "Euclid";
  src: url("../fonts/EuclidCircularA-Regular.ttf");
}
@font-face {
  font-family: "Euclid Bold";
  src: url("../fonts/EuclidCircularA-Bold.ttf");
}
* {
  transition: all 0.3s;
}

.titles {
  font-family: "Lemon";
}

.text-body {
  font-family: "Euclid";
}

.text-body-bold {
  font-family: "Euclid Bold";
}

.text-black {
  color: #2A2A2A;
}

.text-primary {
  color: #07495D;
}

.text-secondary {
  color: #F39131;
}

.text-primary-light {
  color: #DAF7F6;
}

.text-secondary-light {
  color: #F7D560;
}

.text-red {
  color: #D70000;
}

.text-gray {
  color: #797878;
}

.border-primary {
  border-color: #07495D;
}

.bg-primary {
  background-color: #07495D;
}

.bg-secondary {
  background-color: #F39131;
}

.bg-black {
  background-color: #2A2A2A;
}

.bg-primary-light {
  background-color: #DAF7F6;
}

.bg-secondary-light {
  background-color: #F7D560;
}

.bg-light-gray {
  background-color: #F5F6F8;
}

.bg-light-light {
  background-color: #F7F7F7;
}

.btn-primary {
  background-color: #F39131;
}
.btn-primary:hover {
  background-color: #f57900;
}

.btn-secondary {
  background-color: #07495D;
}
.btn-secondary:hover {
  background-color: #02313f;
}

.nav-bar {
  background-color: #fff;
}

.nav-item {
  position: relative;
}
.nav-item::after {
  transition: all 0.2s;
  content: "";
  position: absolute;
  bottom: -1.3em;
  width: 0;
  height: 5px;
  background: #07495D;
  left: 2px;
  border-radius: 15px;
}
@media (max-width: 768px) {
  .nav-item::after {
    bottom: -0.8em;
  }
}
.nav-item:hover {
  color: #07495D;
  font-weight: bold;
}
.nav-item:hover::after {
  width: 100%;
}

.nav-active {
  color: #07495D;
  font-weight: bold;
  position: relative;
}
.nav-active::after {
  content: "";
  position: absolute;
  bottom: -1.3em;
  width: 100%;
  height: 5px;
  background: #07495D;
  left: 2px;
  border-radius: 15px;
}
@media (max-width: 768px) {
  .nav-active::after {
    bottom: -0.8em;
  }
}

.menu-burger {
  height: 5px;
  background: #07495D;
  width: 2.2em;
  border-radius: 15px;
  position: relative;
  cursor: pointer;
  transition: all 0.3s;
}
.menu-burger::after, .menu-burger::before {
  content: "";
  height: 5px;
  background: #07495D;
  width: 2.2em;
  border-radius: 15px;
  transition: all 0.3s;
}
.menu-burger::after {
  left: 0;
  position: absolute;
  bottom: 15px;
}
.menu-burger::before {
  left: 0;
  position: absolute;
  bottom: -15px;
}

.burger-active.menu-burger {
  background-color: rgba(0, 0, 0, 0);
}
.burger-active.menu-burger::after, .burger-active.menu-burger::before {
  opacity: 1;
}
.burger-active.menu-burger::after {
  transform: rotate(45deg);
  top: 0;
  background: #07495D;
}
.burger-active.menu-burger::before {
  transform: rotate(-45deg);
  top: 0;
  background: #07495D;
}

.nav-bar-content {
  height: auto;
  transition: all 0.2s;
}
@media (max-width: 1024px) {
  .nav-bar-content {
    height: 140px;
    overflow: hidden;
  }
}
@media (max-width: 768px) {
  .nav-bar-content {
    height: 100px;
    overflow: hidden;
  }
}

.nav-bar-content-scroll {
  transition: all 0.2s;
  height: 95px;
}

.carousel {
  display: inline-flex;
  overflow-x: hidden;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.input-with-placeholder input + label {
  margin-top: 0.5em;
}

.input-with-placeholder input:focus + label,
.input-with-placeholder input:valid + label,
.input-with-placeholder textarea:focus + label,
.input-with-placeholder textarea:valid + label {
  font-size: 0.7em;
  margin-top: 1px;
}

.linea-search::after {
  content: "";
  position: absolute;
  border: solid 1px #cbcbcb;
  height: 2em;
  top: -0.4em;
  right: 3em;
}

.pages {
  border-color: #07495D;
}

.bg-gradient-emprendedor {
  background-image: linear-gradient(180deg, transparent 0, rgba(0, 0, 0, 0.3) 20%, #fff);
}

.content-accordion {
  transition: all 0.5s cubic-bezier(0, 1, 0, 1);
  max-height: 0;
  overflow: hidden;
}

.content-accordion.show {
  height: auto;
  max-height: 9999px;
  transition: all 0.5s cubic-bezier(1, 0, 1, 0);
}


